import { Component, Fragment } from 'react'; 
import { DragDrop, StatusBar } from '@uppy/react';  
import { bytesToSize , loadImage} from '../../../../utils'
import UppyUpload from '../../../../../../../../../../utils/uppyUpload';
import Uploadering from './uppyLoader'
class Uploads extends Component {
    state = { isLoading: false, selectedFile: null, selectedUrl: null, type: null, thumb_file: "", fileSize: false, throwError: false, videoPath: "", fileSizeText: "", mediaHeight: "", mediaWidth: "" }
 
 
 
    uppyUploader = new UppyUpload();
    componentDidMount() {

        this.uppyUploader.uppy.on('transloadit:result', (stepName, result) => {
            this.setState({  mediaHeight: result.meta.height, mediaWidth: result.meta.width, type: result.mime.includes("video") ? 5 : 4, selectedFile: result.id, selectedUrl: `https://cloud.taggbox.com/onsiteupload/images/${result.id}` }, () => this.onUpdateLocalState())
        })

    }

    onUpdateLocalState = async () => {
        const { updateImageWithData } = this.props;
        const { selectedUrl, type, thumb_file, videoPath, mediaHeight, mediaWidth } = this.state;
        var imgheight = ""; var imgWidth = "";

        if (type == 5) {
            const image = await loadImage(type == 5 ? thumb_file : selectedUrl);
            imgheight = image.height;
            imgWidth = image.width;
        }
        const data = {
            "file": selectedUrl,
            "caption": "",
            "review": "",
            "rating": 0,
            "error": false,
            "activeTab": 1,
            "type": type,
            "thumb_file": type == 5 ? thumb_file : selectedUrl,
            "videoPath": videoPath,
            "mediaHeight": type == 5 ? imgheight : mediaHeight,
            "mediaWidth": type == 5 ? imgWidth : mediaWidth
        }
        updateImageWithData(data)
        this.setState({
            selectedFile: null,
            selectedUrl: null,
            type: null,
            thumb_file: "",
            fileSize: false,
            fileSizeText: "",
            videoPath: "",
            mediaHeight: "",
            mediaWidth: ""
        }, () => this.uppyUploader.uppy.reset())
    }
    render() {
        const { isLoading, fileSize, throwError, fileSizeText } = this.state;
        return <>
            <div className="submit_pic_uploader_wrap">
                <DragDrop
                    className="submit_pic_uploader"
                    note=""
                    uppy={this.uppyUploader.uppy}
                    allowMultipleFiles={false}

                    locale={{
                        strings: {
                            // Text to show on the droppable area.
                            // `%{browse}` is replaced with a link that opens the system file selection dialog.
                            dropHereOr: '',
                            // Used as the label for the link that opens the system file selection dialog.
                            browse: '',
                        },
                    }}
                />
                <div className="submit_pic_uppy_content">
                    <div className="submit_pic_uppy_h">
                        Drag a video / image to upload
                    </div>
                    <div className="submit_pic_uppy_p">
                        Video Size : Max 100MB | Photo Size : Max 50MB | Max 3 Posts
                    </div>
                </div>
            </div>
            <div className="submit_pic_uploader_status">
                <StatusBar
                    uppy={this.uppyUploader.uppy}
                    hideUploadButton
                    hideAfterFinish={false}
                    showProgressDetails
                />
                {isLoading ? <Uploadering /> : null}
                {fileSize ? <div className="submit_pic_limit_error submit_limit_size" style={{ marginTop: 5, display: `flex`, alignItems: `center` }}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                        <g id="Stockholm-icons-/-Code-/-Info-circle" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                            <rect id="bound" x="0" y="0" width="24" height="24" />
                            <circle id="Oval-5" fill="#db4b20" opacity="0.3" cx="12" cy="12" r="10" />
                            <rect id="Rectangle-9" fill="#db4b20" x="11" y="10" width="2" height="7" rx="1" />
                            <rect id="Rectangle-9-Copy" fill="#db4b20" x="11" y="7" width="2" height="2" rx="1" />
                        </g>
                    </svg>
                    File Size ({fileSizeText}) Limit Exceeded.Video Size : Max 100MB
                </div> : null}
                {throwError ? <div className="submit_pic_limit_error submit_limit_size" style={{ marginTop: 5, display: `flex`, alignItems: `center` }}>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                        <g id="Stockholm-icons-/-Code-/-Info-circle" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
                            <rect id="bound" x="0" y="0" width="24" height="24" />
                            <circle id="Oval-5" fill="#db4b20" opacity="0.3" cx="12" cy="12" r="10" />
                            <rect id="Rectangle-9" fill="#db4b20" x="11" y="10" width="2" height="7" rx="1" />
                            <rect id="Rectangle-9-Copy" fill="#db4b20" x="11" y="7" width="2" height="2" rx="1" />
                        </g>
                    </svg>
                     Something went wrong , Please try again.
                </div> : null}


            </div>

        </>
    }
}

export default Uploads;